<template>
  <div class="seller" ref="seller">
    <div class="seller-content">
      <div class="overview">
        <div class="overview-top">
          <h4 class="name">{{seller.name}}</h4>
          <div class="rating-wrap">
            <stars size="36" :score="seller.score"></stars>
            <span class="rating-count">({{seller.ratingCount}})</span>
            <span class="sell-count">用售{{seller.ratingCount}}单</span>
          </div>
        </div>
        <div class="overview-bottom">
          <div class="item item-left">
            <span class="text">起送价</span>
            <span class="content">{{seller.minPrice}}<i>元</i></span>
          </div>
          <div class="item item-center">
            <span class="text">商家配送</span>
            <span class="content">{{seller.deliveryPrice}}<i>元</i></span>
          </div>
          <div class="item item-right">
            <span class="text">平均配送时间</span>
            <span class="content">{{seller.deliveryTime}}<i>分钟</i></span>
          </div>
        </div>
        <div class="collect" @click.stop="toggleCollect($event)" :class="{active:isCollect}">
          <span class="icon-favorite"></span>
          <span class="text">{{collectText}}</span>
        </div>
      </div>
      <split></split>
      <div class="desc">
        <h4 class="title">公告与活动</h4>
        <p class="bulletin">{{seller.bulletin}}</p>
        <ul class="supports">
          <li class="support" v-for="item in seller.supports">
            <span class="icon" :class="classMap[item.type]"></span>
            <span class="text">{{item.description}}</span>
          </li>
        </ul>
      </div>
      <split></split>
      <div class="image">
        <h4 class="title">商家实景</h4>
        <div class="image-wrap" ref="pic">
          <ul class="image-list">
            <li class="item" v-for="item in seller.pics">
              <img :src="item" class="item-img">
            </li>
          </ul>
        </div>
      </div>
      <split></split>
      <div class="infos">
        <h4 class="title">商家信息</h4>
        <ul class="info-list">
          <li class="item" v-for="item in seller.infos">{{item}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll';
  import stars from 'components/stars/stars';
  import split from 'components/split/split';
  import {getToLocal, saveToLocal} from 'common/js/store.js';
  export default{
    props: {
      seller: {
        type: Object,
        default() {
          return {};
        }
      }
    },
    data() {
      return {
        isCollect: (() => {
          return getToLocal('collect', false);
        })()
      };
    },
    components: {
      stars,
      split
    },
    created() {
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
    },
    mounted() {
      this.$nextTick(() => {
        this._initScroll();
        this._picScroll();
      });
    },
    watch: {
      'seller'() {
        this.$nextTick(() => {
          this._initScroll();
          this._picScroll();
        });
      }
    },
    methods: {
      _initScroll() {
        if (!this.sellerScroll) {
          this.sellerScroll = new BScroll(this.$refs.seller, {
            click: true
          });
        } else {
          this.sellerScroll.refresh();
        }
      },
      _picScroll() {
        if (!this.picScroll) {
          this.picScroll = new BScroll(this.$refs.pic, {
            scrollX: true
          });
        } else {
          this.picScroll.refresh();
        }
      },
      toggleCollect(event) {
        if (!event._constructed) {
          return;
        }
        this.isCollect = !this.isCollect;
        this.$nextTick(() => {
          saveToLocal('collect', this.isCollect);
        });
      }
    },
    computed: {
      collectText() {
        if (this.isCollect) {
          return '已收藏';
        }
        return '收藏';
      }
    }
  };
</script>

<style rel="stylesheet/stylus" lang="stylus">
  @import "../../common/stylus/mixin.styl";
  .seller
    position:fixed
    z-index:30
    top:174px
    bottom:0
    left:0
    right:0
    overflow:hidden
    .seller-content
      .overview
        position:relative
        padding:18px
        .overview-top
          padding-bottom:18px
          border-1px(rgba(7, 17, 27, 0.1))
          .name
            margin:0px 0px 8px 0px
            font-size:14px
            line-height:14px
            color:rgb(7, 17, 27)
          .rating-wrap
            display:flex
            .rating-count, .sell-count
              font-size:10px
              line-height:18px
              color:rgb(77, 85, 93)
              vertical-align:top
            .rating-count
              margin:0 12px 0 8px

        .overview-bottom
          display:flex
          padding-top:18px
          .item-left, .item-right
            width:30%
          .item-center
            width:40%
            border-left:1px solid rgba(7, 17, 27, 0.1)
            border-right:1px solid rgba(7, 17, 27, 0.1)
          .item
            display:flex
            flex-direction:column
            text-align:center
            .text
              margin-bottom:4px
              font-size:10px
              line-height:10px
              color:rgb(147, 153, 159)
            .content
              font-size:24px
              line-height:24px
              color:rgb(7, 17, 27)
              i
                font-size:10px
        .collect
          position:absolute
          top:18px
          right:18px
          width:36px
          text-align:center
          span
            display:block
          .icon-favorite
            margin-bottom:4px
            font-size:24px
            line-height:24px
            color:#d6d4d9
          .text
            font-size:10px
            line-height:10px
            color:#d6d4d9
          &.active
            .icon-favorite
              color:rgb(240, 20, 20)
            .text
              color:rgb(77, 85, 93)
      .desc
        padding:18px 18px 0px 18px
        .title
          margin:0px 0px 12px 0px
          font-size:14px
          line-height:14px
          color:rgb(7, 17, 27)
        .bulletin
          margin:0px 12px 16px 12px
          font-size:12px
          line-height:24px
          color:rgb(240, 20, 20)
          letter-spacing:2px
        .supports
          .support
            padding:16px 12px
            border-top:1px solid rgba(7, 17, 27, 0.1)
            icon-image(4)
            .icon
              display:inline-block
              margin-right:6px
              width:16px
              height:16px
              vertical-align:top
            .text
              font-size:12px
              line-height:16px
              color:rgb(7, 17, 27)
      .image
        padding:18px
        .title
          margin:0px 0px 12px 0px
          font-size:14px
          line-height:14px
          color:rgb(7, 17, 27)
        .image-wrap
          width:100%
          overflow:hidden
          font-size:0px
          .image-list
            display:table
            white-space:nowrap
            .item + .item
              margin-left:6px
            .item
              display:inline-block
              width:120px
              height:90px
              .item-img
                width:100%
                height:100%
      .infos
        padding:18px 18px 0px 18px
        .title
          margin:0px 0px 12px 0px
          font-size:14px
          line-height:14px
          color:rgb(7, 17, 27)
        .info-list
          .item
            padding:16px 12px
            border-top:1px solid rgba(7, 17, 27, 0.1)
            font-size:12px
            line-height:16px
            color:rgb(7, 17, 27)
</style>



