<template>
  <transition name="fade">
    <div class="food" v-show="isShowFood" ref="food">
      <div class="food-content">
        <div class="header">
          <img :src="food.image" class="image">
          <div class="btn-close" @click.stop="hide">
            <span class="icon-arrow_lift"></span>
          </div>
        </div>
        <div class="info">
          <h4 class="name">{{food.name}}</h4>
          <div class="count-rating">
            <span class="count">月售{{food.sellCount}}</span>
            <span class="rating">好评率{{food.rating}}%</span>
          </div>
          <div class="price-wrap">
            <span class="price"><i>¥</i>{{food.price}}</span>
            <span class="old-price" v-show="food.oldPrice"><i>¥</i>{{food.oldPrice}}</span>
          </div>
          <transition name="btn-fade">
            <div class="add-cart" v-show="!food.count || food.count===0" @click.stop="addCart($event)">加入购物车</div>
          </transition>
          <transition name="btn-fade">
            <div class="control-wrap" v-show="food.count || food.count!==0">
              <cartcontrol :food="food"></cartcontrol>
            </div>
          </transition>
        </div>
        <split></split>
        <div class="desc-wrap">
          <h4 class="title">商品介绍</h4>
          <p class="desc">{{food.info}}</p>
        </div>
        <split></split>
        <div class="rating-wrap">
          <h4 class="title">商品评价</h4>
          <ratingselect :ratings="food.ratings" :desc="desc" :select-type="selectType" :only-content="onlyContent"
                        @select="selectRatings" @toggle="toggleContent"></ratingselect>
          <ul class="rating-list">
            <li class="rating-item border-1px" v-for="item in food.ratings" v-show="needShow(item.rateType,item.text)">
              <span class="time">{{item.rateTime | formatTime}}</span>
              <div class="rating-content">
                  <span class="icon"
                        :class="{'icon-thumb_up':item.rateType === 0,'icon-thumb_down':item.rateType===1}"></span>
                <span class="text">{{item.text}}</span>
              </div>
              <div class="user">
                <span class="username">{{item.username}}</span>
                <img :src="item.avatar" class="avatar">
              </div>
            </li>
          </ul>
          <div class="not-rating" v-show="!food.ratings">
            暂无评论
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import Vue from 'vue';
  import BScroll from 'better-scroll';
  import {formatDate} from 'common/js/date.js';
  import split from 'components/split/split';
  import ratingselect from 'components/ratingselect/ratingselect';
  import cartcontrol from 'components/cartcontrol/cartcontrol';
  /* eslint-disable no-unused-vars */
  const POSITIVE = 0;
  /* eslint-disable no-unused-vars */
  const NEGATIVE = 1;
  const ALL = 2;
  export default{
    props: {
      food: {
        type: Object
      }
    },
    data() {
      return {
        isShowFood: false,
        selectType: ALL,
        onlyContent: false,
        desc: {
          all: '全部',
          positive: '推荐',
          negative: '吐槽'
        }
      };
    },
    methods: {
      addCart(event) {
        if (!event._constructed) {
          return;
        }
        if (!this.food.count) {
          Vue.set(this.food, 'count', 1);
        } else {
          this.food.count++;
        }
      },
      needShow(type, text) {
        if (!text && this.onlyContent === true) {
          return false;
        }
        if (this.selectType === ALL) {
          return true;
        }
        return this.selectType === type;
      },
      selectRatings(type) {
        this.selectType = type;
        this.$nextTick(() => {
          this._initScroll();
        });
      },
      toggleContent() {
        this.onlyContent = !this.onlyContent;
        this.$nextTick(() => {
          this._initScroll();
        });
      },
      show() {
        this.isShowFood = true;
        this.$nextTick(() => {
          this._initScroll();
        });
      },
      hide() {
        this.isShowFood = false;
        this.$emit('hide');
      },
      _initScroll() {
        if (!this.foodScroll) {
          this.foodScroll = new BScroll(this.$refs.food, {
            click: true
          });
        } else {
          this.foodScroll.refresh();
        }
      }
    },
    filters: {
      formatTime(time) {
        let date = new Date(time);
        return formatDate(date, 'yyyy-MM-dd hh:mm');
      }
    },
    components: {
      split,
      ratingselect,
      cartcontrol
    }
  };
</script>

<style rel="stylesheet/stylus" lang="stylus">
  @import "../../common/stylus/mixin.styl";
  .food
    position:fixed
    top:0
    left:0
    right:0
    bottom:48px
    z-index:40
    overflow:hidden
    background-color:#fff
    &.fade-enter-active, &.fade-leave-active
      transition:all 0.2s linear
    &.fade-enter, &.fade-leave-active
      transform:translate(100%, 0);
    .food-content
      .header
        position:relative
        width:100%
        height:0
        padding-top:100%
        .image
          position:absolute
          top:0
          left:0
          width:100%
          height:100%
        .btn-close
          position:absolute
          top:10px
          left:10px
          padding:6px 8px 6px 6px
          border-radius:50%
          background-color:rgba(7, 17, 27, 0.6)
          .icon-arrow_lift
            font-size:18px
            color:#fff
      .info
        position:relative
        padding:18px
        .name
          margin:0 0 8px 0
          font-size:14px
          font-weight:700
          line-height:14px
          color:rgb(7, 17, 27)
        .count-rating
          margin-bottom:18px
          .count, .rating
            font-size:10px
            line-height:10px
            color:rgb(147, 153, 159)
          .count
            margin-right:12px
        .price-wrap
          .price
            margin-right:8px
            font-size:14px
            font-weight:700
            line-height:24px
            color:rgb(240, 20, 20)
            i
              font-size:10px
              font-weight:200
          .old-price
            font-size:10px
            font-weight:700
            line-height:24px
            color:rgb(147, 153, 159)
            text-decoration:line-through
            i
              font-weight:200
        .add-cart
          position:absolute
          right:18px
          bottom:18px
          padding:6px 12px
          font-size:10px
          line-height:12px
          color:#ffffff
          background-color:rgb(0, 160, 220)
          border-radius:24px
          &.btn-fade-enter-active,&.btn-fade-leave-active
            transition:all 0.2s linear
          &.btn-fade-enter,&.btn-fade-leave-active
            opacity:0
            transform:scale(2,0)
        .control-wrap
          position:absolute
          right:10px
          bottom:10px
      .desc-wrap
        padding:18px
        .title
          margin:0 0 6px 0
          font-size:14px
          font-weight:700
          line-height:14px
          color:rgb(7, 17, 27)
        .desc
          margin:0 8px
          font-size:12px
          line-height:24px
          color:rgb(77, 85, 93)
          letter-spacing:2px
      .rating-wrap
        .title
          margin:18px
          font-size:14px
          font-weight:700
          line-height:14px
          color:rgb(7, 17, 27)
        .rating-list
          padding:0 18px
          .rating-item
            position:relative
            padding:16px 0
            border-1px(rgba(7, 17, 27, 0.1))
            &:last-child:after
              display:none
            .time
              font-size:10px
              line-height:12px
              color:rgb(147, 153, 159)
            .rating-content
              margin-top:6px
              .icon
                margin-right:4px
                font-size:12px
                line-height:24px
                vertical-align:top
                &.icon-thumb_up
                  color:rgb(0, 160, 220)
                &.icon-thumb_down
                  color:rgb(147, 153, 159)
              .text
                font-size:12px
                line-height:22px
                color:rgb(7, 17, 27)
                vertical-align:top
            .user
              position:absolute
              top:16px
              right:0
              .username
                margin-right:6px
                font-size:10px
                line-height:12px
                color:rgb(147, 153, 159)
                vertical-align:top
              .avatar
                width:12px
                height:12px
                border-radius:50%
                vertical-align:top
        .not-rating
          padding:16px 18px
          font-size:12px
          color:rgb(147, 153, 159)
</style>



