<template>
  <div class="header">
    <div class="header-container">
      <div class="header-bg">
        <img :src="seller.avatar" class="bg">
      </div>
      <div class="header-top">
        <img :src="seller.avatar" class="avatar">
        <div class="top-content">
          <div class="title">
            <span class="icon"></span>
            <span class="text">{{seller.name}}</span>
          </div>
          <div class="desc-wrap">
            <span class="desc">{{seller.description}}</span>
            <span class="delivery-time">/{{seller.deliveryTime}}分钟送达</span>
          </div>
          <div class="supports-wrap">
            <span class="icon" :class="classMap[seller.supports[0].type]"></span>
            <span class="text">{{seller.supports[0].description}}</span>
          </div>
          <div class="count-wrap" @click.stop = "showDetail">
            <span>{{seller.supports.length}}个</span><span class="icon-keyboard_arrow_right"></span>
          </div>
        </div>
      </div>
      <div class="header-bulletin" @click.stop = "showDetail">
        <span class="icon"></span>
        <span class="bulletin">{{seller.bulletin}}</span>
        <span class="icon-keyboard_arrow_right"></span>
      </div>
    </div>
    <transition name="fade-detail">
      <div class="detail" v-show="toggleDetail">
        <div class="detail-wrap">
          <div class="detail-main">
            <h2 class="title">{{seller.name}}</h2>
            <div class="stars-wrap">
              <stars :size="48" :score="seller.score"></stars>
            </div>
            <div class="supports-wrap">
              <titleline text="优惠信息"></titleline>
              <ul class="supports-list">
                <li class="supports-item" v-for="item in seller.supports">
                  <span class="icon" :class="classMap[item.type]"></span>
                  <span class="text">{{item.description}}</span>
                </li>
              </ul>
            </div>
            <div class="bulletin-wrap">
              <titleline text="商家公告"></titleline>
              <p class="bulletin">{{seller.bulletin}}</p>
            </div>
          </div>
        </div>
        <div class="btn-wrap" @click.stop = "hideDetail">
          <span class="icon-close"></span>
        </div>
      </div>
    </transition>
  </div>
</template>

<script type="text/ecmascript-6">
  import stars from 'components/stars/stars';
  import titleline from 'components/titleline/titleline';
  export default{
    props: {
      seller: {
        type: Object,
        default: {}
      }
    },
    data() {
      return {
        toggleDetail: false
      };
    },
    created() {
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
    },
    components: {
      stars,
      titleline
    },
    methods: {
      hideDetail() {
        this.toggleDetail = false;
      },
      showDetail() {
        this.toggleDetail = true;
      }
    }
  };
</script>

<style rel="stylesheet/stylus" lang="stylus">
  @import "../../common/stylus/mixin.styl";
  .header
    width:100%
    .header-container
      position:relative
      width:100%
      color:#fff
      background-color:rgba(7, 17, 27, 0.5)
      .header-bg
        position:absolute
        top:0
        left:0
        z-index:-1
        width:100%
        height:100%
        overflow:hidden
        .bg
          width:100%
          filter:blur(10px)
      .header-top
        display:flex
        padding:24px 12px 0px 24px
        margin-bottom:18px
        .avatar
          margin-right:16px
          width:64px
          height:64px
          border-radius:2px
        .top-content
          .title
            margin-top:2px
            margin-bottom:8px
            font-size:0px
            .icon
              display:inline-block
              margin-right:6px
              width:30px
              height:18px
              bg-image('./images/brand')
              vertical-align:top
            .text
              font-size:16px
              font-weight:bold
              line-height:18px
          .desc-wrap
            margin-bottom:10px
            font-size:12px
            line-height:12px
          .supports-wrap
            icon-image(1)
            .icon
              display:inline-block
              margin-right:4px
              width:12px
              height:12px
              vertical-align:top
            .text
              font-size:10px
              line-height:12px
          .count-wrap
            position:absolute
            right:12px
            top:66px
            padding:7px 8px
            font-size:10px
            line-height:12px
            background-color:rgba(0, 0, 0, 0.2)
            border-radius:26px
            .icon-keyboard_arrow_right
              display:inline-block
              vertical-align:top
      .header-bulletin
        display:flex
        padding:0 12px
        width:100%
        height:28px
        background-color:rgba(7, 17, 27, 0.2)
        .bulletin
          flex:1
          overflow:hidden
          white-space:nowrap
          text-overflow:ellipsis
          font-size:10px
          line-height:28px
        .icon
          margin-top:8px
          margin-right:4px
          width:22px
          height:12px
          line-height:28px
          bg-image('./images/bulletin')
        .icon-keyboard_arrow_right
          display:inline-block
          margin-left:4px
          vertical-align:top
          line-height:28px
    .detail
      position:fixed
      top:0
      bottom:0
      left:0
      right:0
      z-index:100
      overflow-y:auto
      background-color:rgba(7, 17, 27, 0.8)
      backdrop-filter:blur(10px)
      &.fade-detail-enter-active,&.fade-detail-leave-active
        transition:all 0.5s
      &.fade-detail-enter,&.fade-detail-leave-active
        opacity:0
      .detail-wrap
        width:100%
        min-height:100%
        .detail-main
          width:100%
          padding:64px 36px
          .title
            margin:0px 0px 16px 0px
            font-size:16px
            font-weight:700
            line-height:16px
            color:#fff
            text-align:center
          .stars-wrap
            margin-bottom:28px
          .supports-wrap
            .supports-list
              margin:24px 12px 28px
              .supports-item
                margin-bottom:12px
                &.supports-item:last-child
                  margin-bottom:0px
                .icon
                  display:inline-block
                  margin-right:6px
                  width:16px
                  height:16px
                  vertical-align:top
                icon-image(2)
                .text
                  font-size:12px
                  line-height:16px
                  color:#fff
          .bulletin-wrap
            .bulletin
              margin:24px 12px
              font-size:12px
              line-height:24px
              color:#fff
              letter-spacing:2px
      .btn-wrap
        margin-top:-64px
        width:100%
        text-align:center
        .icon-close
          font-size:32px
          color:rgba(255, 255, 255, 0.5)
</style>



